* {
  margin: 0;
  padding: 0;
}
/* -- Desktop Device css -- */
.App{
  min-width: 1024px;
  max-width: 1920px;
}
.header {
  min-width: 1024px;
  max-width: 1920px;
  position: fixed;
  width: 100%;
  z-index: 999;
  top: 0;
}
.main,
.footer{
  min-width: 1024px;
  max-width: 1920px;
}

.Company-Logo {
  position: absolute;
  top: 0;
  right: 70vw;
  z-index: 1;
}

.Company-Logo-Container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #4BBEFF;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
  width: 256px;
  height: 127px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
}

.Language-Select {
  display: flex;
  justify-content: flex-end;
  box-sizing: border-box;
  height: 36px;
  background-color: #ffffff;
  width: 100%;
  box-shadow: 0px 2px 5px gray;
  align-items: center;
}

.Language-Select ul {
  display: flex;
  justify-content: flex-end;
  gap: 20px;
  margin-right: 178px;
}

.Language-Select ul li {
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  width: 72px;
}

.Language-Select ul li a {
  text-decoration: none;
  color: black;
  font-size: 0.75em;
}

.Language-Select-Icon {
  display: none;
}

.Header-Nav-Menu ul {
  display: flex;
  align-items: center;
  gap: 20px;
}

.Header-Nav-Menu ul li {
  display: flex;
  box-sizing: border-box;
  width: 72px;
  list-style: none;
  justify-content: center;
  color: #ffffff;
}

.Header-Nav-Menu ul li a {
  text-decoration: none;
  color: #ffffff;

  font-size: 1em;
  text-transform: uppercase;
  margin: 0;
}

.Header-Nav-Menu ul li a::after {
  display: block;
  content: '';
  border-bottom: 2px solid #ffffff;
  transform: scaleX(0);
  transition: transform 0.2s ease-out;
}

.Header-Nav-Menu ul li a:hover::after {
  transform: scaleX(1);
  transform-origin: 50%;

}

.Header-Menu-Icon {
  display: none;
}


.Header-Container {
  position: absolute;
  width: 100%;
  background-color: rgba(0, 0, 0, 50%);
  border-bottom: 2px solid #ffffff;
  display: flex;
  justify-content: flex-end;
}

.Header-Menu-Container {
  box-sizing: border-box;
  height: 64px;
  width: 65%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Header-Side-Menu {
  margin-right: 178px;
  display: flex;
  gap: 20px;
  z-index: 1;
}

.Header-Side-Menu div {
  box-sizing: border-box;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Header-Side-Menu div a {
  color: #ffffff;
  width: 72px;
  text-decoration: none;
  font-size: 0.75em;
}

.Header-Side-Menu .Login-Form {
  display: block;
  position: absolute;
  top: 66px;
  width: 260px;
  height: auto;
  border-radius: 5px;
  border: 5px solid #4BBEFF;
  background-color: #ffffff;
}

.Header-Side-Menu .Login-Form h4 {
  margin: 10px 0px;
}

.Login-Form .Input-Id,
.Login-Form .Input-Password {
  width: 208px;
  padding-left: 10px;
  height: 25px;
  margin: 5px 0;
  border: 2px solid #4BBEFF;
  border-radius: 5px;
}

.Serch-User-Info {
  display: flex;
}

.Login-Form .Serch-User-Info a {
  color: #969696;
  width: auto;
  font-size: 0.8em;
}

.Login-Form .Serch-User-Info label {
  margin: 10px 4px;
}

.Login-Form .Serch-User-Info input {
  display: inline;
  width: 69px;
  height: 24px;
  margin: 10px 0;
  margin-left: 5px;
  color: #ffffff;
  font-weight: bold;
  font-size: 0.8em;
  background-color: #C3C3C3;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.Login-Form .Serch-User-Info input:active{
  background-color : #4BBEFF
}

.Login-Form .Remember-Id-Checkbox {
  display: flex;
  justify-content: left;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 22px;
  font-size: 0.8em;
}

.Login-Form .Login-Btn {
  width: 222px;
  height: 30px;
  color: #ffffff;
  font-weight: bold;
  font-size: 1em;
  background-color: #4BBEFF;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.Login-Form .Login-Btn:active {
  background-color: #0094ea;
}


.Serch-Box {
  display: none;
  width: none;
}

.Slide {
  margin-top: 36px;
  position: relative;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
  width: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
}

.Slide-Container {

  width: 100%;
  height: 580px;
  margin: 0 auto;
  position: relative;
}

.Slide-Container ul {
  list-style: none;
}

.Slide-Container img {
  position: absolute;
  z-index: -1;
  top: 0%;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
}

.Slide-Container .active {
  display: block;
}

.Slide-Container li:not(.active) {
  display: none;
}

.Control-Remort {
  position: absolute;
  top: 300px;
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 70%;
}

.fade-in {
  opacity: 1;
  transition: opacity 0.18s ease-in-out;
}

.fade-out {
  opacity: 0;
  transition: opacity 0.18s ease-in-out;
}

.Control-Remort .Prev {
  left: 0;
  cursor: pointer;
}

.Control-Remort .Next {
  right: 0;
  cursor: pointer;
}

.Select-menu {
  position: absolute;
  top: 550px;
  display: flex;
  gap: 30px;
  left: 50vw
}

.Select-menu .Destination,
.Select-menu .Departure-Date {
  border-bottom: 2px solid #ffffff;
}

.Select-City {
  display: flex;
}

.Destination,
.Departure-Date {
  font-size: 1.5em;
  color: #ffffff;
  font-weight: bold;
  cursor: pointer;
}

.Destination-Select {
  display: block;
  position: absolute;
  background-color: #ffffff;
  border: #4BBEFF solid 5px;
  border-radius: 5px;
  height: auto;
  letter-spacing: -0.25px;
  margin-top: 7px;
  padding: 10px;
  width: fit-content;
  z-index: 100;
}

.Destination,
.Departure-Date {
  padding-bottom: 5px;
}


.Country-List {
  width: 200px;
  height: 350px;
  border: 2px solid #3F4687;
  background-color: #E2EAF1;
}

.Country-List button {
  padding: 5px;
  display: block;
  width: 200px;
  height: 40px;
  font-size: 1em;
  font-weight: bold;
  text-align: left;
  background-color: #E2EAF1;
  border: none;
  border-radius: none;
}

.Country-List button.active {
  background-color: #0CA7FF;
  color: #ffffff;
}

.City-List {
  height: 350px;
  width: 400px;
  border-top: 2px solid #3F4687;
  border-right: 2px solid #3F4687;
  border-bottom: 2px solid #3F4687;

  text-align: center;
}

.City-List ul {
  display: block;
  height: 310px;
  overflow-y: scroll;
}

.City-List h3 {
  margin-top: 5px;
}

.City-List ul li button {
  display: block;
  margin: 5px;
  padding-left: 5px;
  width: 400px;
  height: 42px;
  font-size: 1em;
  text-align: left;
  background-color: #E2EAF1;
  border: none;
  border-radius: none;
}

.City-List ul li button.active {
  background-color: #0CA7FF;
  color: #ffffff;
  font-weight: bold;
}

.Calendar-Container {
  position: absolute;
  z-index: 5;
  top:40px
}

.Package-Trip-Container {
  position: absolute;
  top: 616px;
  right: 70vw;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #4BBEFF;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
  width: 256px;
  height: 127px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
}

.Package-Trip-Text {
  color: #ffffff;
  text-align: center;
}

.Package-Trip-Text-Container p,
.Package-Trip-Text-Container h4 {
  width: 100%;
  padding-bottom: 5px;
}

.Package-Trip-Text h4 {
  font-size: 1.5em;
  font-weight: bold;
}

.Package-Trip-Text p {
  font-size: 0.8em;
  font-weight: bold;
}

.Package-Trip {
  margin-top: 142px;
}

.Show-Package-Trip-Container {
  display: flex;
  gap: 20px;
  justify-content: center;
  align-items: center;
  z-index: -1;
}

.Package-Trip-Text-Container {
  padding: 5px 10px;
}

.Package-Trip-1,
.Package-Trip-2,
.Package-Trip-3 {
  max-width: 348px;
  height: 334px;
  border: 1px solid #7d7d7d;
  border-radius: 5px;
  cursor: pointer;
}

.Package-Trip-1:hover,
.Package-Trip-2:hover,
.Package-Trip-3:hover {
  transform: translateY(-0.25em);
  box-shadow: 2px 2px 5px 5px rgba(115, 115, 115, 0.3);
  transition: transform 0.1s ease-in-out;
}

.Package-Trip-1:not(:hover),
.Package-Trip-2:not(:hover),
.Package-Trip-3:not(:hover) {
  transform: translateY(0em);
  transition: transform 0.1s ease-in-out;
}

.Trip-Type {
  color: red;
  font-weight: bold;
  font-size: 1em;
}

.Popularity-Trip-Title {
  display: flex;
  justify-content: center;
}

.Popularity-Trip-Title-Container {
  text-align: center;
  margin: 20px 0;
}

.Popularity-Trip-Title-Container h4 {
  font-size: 1.5em;
  font-weight: bold;
  margin-bottom: 5px;
}

.Popularity-Trip-Title-Container p {
  font-size: 0.8em;
}

.Popularity-Trip-List-Container {
  margin: auto;
  display: flex;
  width: 1100px;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.Popularity-Trip-List-1,
.Popularity-Trip-List-2,
.Popularity-Trip-List-3,
.Popularity-Trip-List-4 {
  position: relative;
  display: block;
  height: 310px;
  overflow: hidden;
}

.Popularity-Trip-List-1:hover img,
.Popularity-Trip-List-2:hover img,
.Popularity-Trip-List-3:hover img,
.Popularity-Trip-List-4:hover img {
  transform: scale(1.1);
  transition: transform 0.2s ease-in-out;
}

.Popularity-Trip-List-1:not(:hover) img,
.Popularity-Trip-List-2:not(:hover) img,
.Popularity-Trip-List-3:not(:hover) img,
.Popularity-Trip-List-4:not(:hover) img {
  transform: scale(1);
  transition: transform 0.2s ease-in-out;
}

.Popularity-Trip-List-Text {
  color: #ffffff;
  box-sizing: border-box;
  bottom: 0px;
  position: absolute;
  padding: 5px 10px;
  height: 30%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.Popularity-Trip-List-Text a {
  text-decoration: none;
  color: #ffffff;
}

.Popularity-Trip-List-Text h4 {
  margin-bottom: 10px;
}

.Banner-ad {
  width: 100%;
}

.Banner-ad-Text {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
}

.Banner-ad-Text-Container {
  text-align: center;
  margin: 40px 0;
}

.Banner-ad-Text-Container h4 {
  font-size: 1.5em;
}

.Banner-ad-Text-Container p {
  font-size: 1em
}

.Banner-ad-Container {
  width: 1084px;
  overflow-x: hidden;
  margin: auto;
}

.Banner-ad-Container ul {
  display: flex;
  list-style: none;
}

.Recommend-Trip-Container {
  position: relative;
  margin: 15px 0;

}

.Recommend-Trip-Text {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Recommend-Trip-Text {
  margin: 10px 0px;
}

.Recommend-Trip-Text-Container h4 {
  font-weight: bold;
  font-size: 1.5em;
}

.Recommend-Trip-Text-Container P {
  font-size: 0.8em;
}

.recommend-trip-list {
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 700px;
  margin: 0 auto;
  height: 250px;
  overflow: hidden;
}

.recommend-trip-list ul {
  display: flex;
  justify-content: center;
  gap: 40px;
  list-style: none;
  transition: transform 0.5s ease-in-out;
}

.recommend-trip-list ul li {
  text-align: center;
}

.recommend-trip-list ul li img {
  display: block;
}

.recommend-trip-list ul li p {
  text-align: center;
  display: block;
  margin: 10px 0px;
}

.recommend-slide-remote {
  position: absolute;
  top: 50%;
  width: 100%;
  gap: 1048px;
  display: flex;
  justify-content: center;
}

.recommend-prev,
.recommend-next {
  background-color: transparent;
  border: none;
}

.Best-Trip-Review-Container {
  position: relative;
  display: flex;
  justify-content: center;
  margin: auto;
  background-color: #D2E4EC;
  padding: 15px 0 35px 0;
}

.Best-Trip-Review-Text-Container {

  width: 1084px;
}

.Best-Trip-Review-Text {
  text-align: left;
  margin: 20px 0;
}

.Best-Trip-Review-Text h4 {
  font-size: 1.5em;
}

.Best-Trip-Review-thumbnail-Container {
  position: relative;
  display: flex;
  justify-content: center;
  gap: 20px;
}

.Best-Trip-Review-thumbnail-Container h4 {
  margin-bottom: 5px;
}

.Best-Trip-Review-thumbnail-Container h4,
.Best-Trip-Review-thumbnail-Container p {
  font-size: 1em;
  color: #ffffff;
  font-weight: bold;
}

.Review-1 {
  position: relative;
  box-sizing: border-box;
  height: 478px;
}

.Review-2 {
  position: relative;
  box-sizing: border-box;
  height: 232px;
}

.Review-3 {
  position: relative;
  box-sizing: border-box;
  margin-top: 20px;
  height: 228px;
  box-sizing: border-box;
  margin: none;
}

.Best-Trip-Review-Title {
  box-sizing: border-box;
  position: absolute;
  bottom: 0;
  padding: 20px 20px;
  height: 110px;
  width: 533px;
  background-color: rgba(0, 0, 0, 0.5);
}

.Go-top button {
  position: fixed;
  bottom: 100px;
  right: 5vw;
  width: 70px;
  height: 70px;
  border-radius: 50px;
  border: none;
  background-color: #4BBEFF;
  color: #ffffff;
  font-size: 1.1em;
  cursor: pointer;
}

.footer {
  background-color: #3e3e3e;
  font-size: 0.95em;

}

.footer-container {
  width: 1100px;
  margin: auto;
  padding: 10px 0;
}

.footer-links {
  padding-bottom: 10px;
}

.footer-links a {
  text-decoration: none;
  color: #ffffff;
  margin: 0px 5px;
  border-bottom: 1px solid transparent;
}

.footer-links a:hover {
  color: #ff2121;
  border-bottom: 1px solid #ff2121;
}

.footer-infomation span {
  color: #b3b3b3;
  margin: 0px 5px;
}

.footer-practice {
  margin: 10px 0px;
  font-size: 0.7em;
  font-weight: bolder;
  text-align: center;
  color: #b3b3b3;
}

.footer-practice a {
  text-decoration: none;
  color: #b3b3b3;
}

.copyright {
  text-align: center;
  color: #b3b3b3;
}

